<template>
    <div id="app">
        <!-- 背景图片 -->
        <div
            class="bg"
            :style="bgImg"
        ></div>
        <div
            class="bg blur"
            :style="bgImg"
        ></div>
      
        <!-- 音乐播放器主体结构 -->
        <div class="music-wrapper">
            <!-- 顶部栏 -->
            <div class="topBar">
                <div class="back"><i class="iconfont">&#xe61e;</i></div>
                <!-- 歌曲信息 -->
                <div class="info">
                    <!-- 标题 -->
                    <div class="title">
                        {{song[pinNum].title}}
                    </div>
                    <!-- 作者 -->
                    <div class="author">
                        {{song[pinNum].author}}
                    </div>
                </div>
                <div class="share"><i class="iconfont share">&#xe639;</i></div>
            </div>
      
            <!-- CD封面 -->
            <div class="main">
                <div
                    class="CD"
                    :style="coverRotate"
                ></div>
            </div>

            <!--音频-->
            <audio 
              id='player' 
              :src='src' 
              v-on:canplay='handleCanPlay' 
              v-on:timeupdate='handleTimeUpdate' 
              v-on:ended='handleEnd'
            ></audio>

            <!-- 进度条 -->
            <div class="progress">
                <div class="curTime">{{transformSecToTime(audioInfo.currentTime)}}</div>
                <div class="progress-line">
                    <div class="whole-line"></div>
                    <div class="already-line" v-bind:style="'width:' + progress + '%'"></div>
                    <div class="play-point" v-bind:style="'left:' + progress + '%'"></div>
                </div>
                <div class="AllTime">{{transformSecToTime(audioInfo.duration)}}</div>
            </div>
      
            <!-- 底部控制条 -->
            <div class="toolBar">
                <div class="heart"><i class="iconfont share">&#xe68d;</i></div>
                <div v-on:click='lastSong' class="last"><i class="iconfont share">&#xe6e1;</i></div>
                <div v-if="!isPlaying" v-on:click='playMusic' class="play"><i class="iconfont share">&#xe6ce;</i></div>
                <div v-if="isPlaying" v-on:click='pauseMusic' class="pause"><i class="iconfont">&#xe643;</i></div>
                <div v-on:click='nextSong' class="next"><i class="iconfont share">&#xe718;</i></div>
                <div class="list"><i class="iconfont share">&#xe6d3;</i></div>
            </div>
        </div>
    </div>

</template>

<script>
/* import player from './components/player.vue' */

export default {
  name: 'App',
  components: {

  },
    data(){
      return{
        // 单曲信息
        song:[
        { title: 'You Make My Life 1UP',
          author: 'Nanahira (ななひら)', 
        },
        { title: '大江戸コントローラー',
          author: 'Yunomi (ゆのみ)', 
        },
        { title: 'Chocolate Missile',
          author: 'アリスシャッハと魔法の楽団', 
        },
        { title: 'タコスぢから=ユメぢから',
          author: '钉宫理惠', 
        },
        { title: 'ドキドキ☆ココロ☆フレーバー',
          author: '榊原由依', 
        }],

        // 指针
        pinNum: 0,

 /*        bgImg: {
          backgroundImage: "url(" + require("@/song/cover"+(pinNum+1)+".jpg") + ")",
        }, */

          /*         
        cover: {
          backgroundImage: "url(" + require("@/song/cover.jpg") + ")",
          transform: "rotate(" + this.rotateDeg + "deg)"
        },
         */

/*         src: require("@/song/song1.mp3"), */
        isPlaying: false,

        rotateDeg: 0,
        clockNum:0,

        // 播放器信息
        audioInfo: {
          duration: 0,
          currentTime: 0
        }
      }
    },
    // 计算属性，使用方法与data一样： this.progress
    computed: {
        progress(){
            var duration = this.audioInfo.duration;
            var currentTime = this.audioInfo.currentTime;
            return (currentTime / duration) * 100;
        },
        coverRotate(){
            return{
              backgroundImage: "url(" + require("@/song/cover"+(this.pinNum)+".jpg") + ")",
              transform: "rotate(" + this.rotateDeg + "deg)"
            };
        },
        bgImg(){
            return{backgroundImage: "url(" + require("@/song/cover"+(this.pinNum)+".jpg") + ")"};
        },
        src(){
            return require("@/song/song"+(this.pinNum)+".mp3");
        }

    },

    methods: {
        // 播放
        playMusic(){
            var player = document.getElementById('player');
            player.play();
            this.isPlaying = true;

            this.voiceStepUp();
            this.startRotate();
        },
        // 暂停
        pauseMusic(){
            var player = document.getElementById('player');
            setTimeout(function(){
                player.pause();
            },1000);
            this.isPlaying = false;

            this.voiceStepOut();
            this.pauseRotate();
        },

        // 封面旋转
        startRotate(){
            var rotateDuration = 6;
            var rotateStep = 0.04;

            var that = this;

            var clockNum = setInterval(function(){
                that.rotateDeg += rotateStep;
            },rotateDuration);

            this.clockNum = clockNum;
        },

        // 暂停封面旋转
        pauseRotate(){
            clearInterval(this.clockNum);
        },

        // 监听音频开始播放
        handleCanPlay(event){
            // 返回当前音频总长度
            this.audioInfo.duration = event.target.duration; 
        },

        // 监听音频当前播放位置
        handleTimeUpdate(event){
            // 返回当前音频播放位置
            this.audioInfo.currentTime = event.target.currentTime
        },
        
        // 监听当前音频是否结束
        handleEnd(){
            this.pauseRotate();
        },



        // 时间格式转换
        transformSecToTime(time){
            var tempTime = Math.floor(time);
            var min = 0;
            var sec = 0;

            if(tempTime > 60){
                min = Math.floor(tempTime / 60);
                sec = tempTime % 60;
            }else{
                sec = tempTime;
            }

            if(min<10) min = '0' + min;
            if(sec<10) sec = '0' + sec;

            return min + ':' +sec;
        },



        // 音量渐变（渐入）
        voiceStepUp(){
            var audio = document.getElementById('player');
            audio.volume = 0;
            var step = 0.05;
            var duration = setInterval(function(){
                audio.volume = Math.min(1,audio.volume+step);
                if(audio.volume >= 1){
                    clearInterval(duration);
                }
            },100)
        },


        // 音量渐变（渐出）
        voiceStepOut(){
            var audio = document.getElementById('player');
            audio.volume = 1;
            var step = 0.05;
            var duration = setInterval(function(){
                audio.volume = Math.max(0,audio.volume-step);
                if(audio.volume <= 0){
                    clearInterval(duration);
                }
            },50)
        },

        // 下一曲
        nextSong(){
          // 清空旋转动画
          this.pauseRotate();
          // 列表循环判断
          if(this.pinNum == 4){
            this.pinNum = 0;
          }else{
          this.pinNum += 1;
          }

          var player = document.getElementById('player');
          var playPromise = player.play();
          // 重载
          player.load();
          // 防止阻塞
          if(playPromise !== undefined){
            playPromise.then(()=>{
              player.play();
            });
            this.isPlaying = true;
            this.voiceStepUp();
            this.startRotate();
          }

          if(this.isPlaying == false)this.startRotate(); 

        },

        // 上一曲
        lastSong(){
          // 清空旋转动画
          this.pauseRotate();
          // 列表循环判断
          if(this.pinNum == 0){
          this.pinNum = 4;
          }else{
          this.pinNum -= 1;
          }
       
          var player = document.getElementById('player');
          var playPromise = player.play();
          // 重载
          player.load();
          // 防止阻塞
          if(playPromise !== undefined){
            playPromise.then(()=>{
              player.play();
            });
            this.isPlaying = true;
            this.voiceStepUp();
            this.startRotate();
          }

          if(this.isPlaying == false)this.startRotate();

        }

    }

}

</script>

